<template>
    <section class="product">
        <Preloader v-if="loading" />
        <div v-else class="container d-flex flex-column">
            <div class="row">
                <div :class="['col-lg-3 col-md-6 col-12 mb-32', 'order-' + idx]" v-for="(product, idx) in products" :key="idx" @click="selectProduct(product, idx)">
                    <ProductsItem 
                        :title="product.title" 
                        :text="product.text" 
                        :image="product.image"
                    />
                </div>
                <transition
                    name="fade"
                    mode="out-in"
                >
                    <div :class="['col-12 mb-32', 'order-' + (selectedIndex|3)]" v-if="selectedProduct.id">
                        <div class="product-info">
                            <div class="row">
                                <div class="col-lg-4 col-12 mb-lg-0 mb-4">
                                    <ProductsItem
                                        :title="selectedProduct.title" 
                                        :text="selectedProduct.text" 
                                        :image="selectedProduct.image"
                                    />
                                </div>
                                <div class="col-lg-8 col-12">
                                    <div class="product-info__block">
                                        <p class="p2 text-grey-1 mb-16">Сюда можно добавить любую необходимую информацию: заголовок, текстовое описание, изображения, ссылки и пр.</p>
                                        <div class="product-info__links">
                                            <a :href="'/static/files/' + file[0]" download class="file__link" v-for="(file, index) in selectedProduct.files" :key="index">
                                                <!-- <svg width="40" height="40" viewBox="0 0 40 40" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M21.102 0H5.237C4 0 3 .933 3 2.083v35.834C3 39.067 4.001 40 5.237 40h29.526C36 40 37 39.067 37 37.917v-23.11L21.102 0zM37 10.225V2.083C37 .933 35.998 0 34.763 0h-8.741L37 10.225z" fill="#EEF5F5"/><path d="M13.38 20.495V23h-1.464v-7.11h2.774c.533 0 1.002.098 1.406.294.407.195.72.473.937.835.218.358.327.766.327 1.225 0 .697-.239 1.247-.717 1.65-.476.4-1.135.601-1.978.601h-1.284zm0-1.186h1.31c.387 0 .681-.092.883-.274.205-.182.308-.443.308-.781 0-.348-.103-.63-.308-.845-.205-.215-.488-.325-.85-.332h-1.342v2.232zM18.372 23v-7.11h2.188c.625 0 1.183.142 1.674.425.495.28.88.68 1.158 1.202.276.517.415 1.106.415 1.767v.327c0 .661-.137 1.249-.41 1.763-.27.514-.653.913-1.148 1.196a3.347 3.347 0 01-1.675.43h-2.202zm1.465-5.923v4.746h.708c.573 0 1.01-.187 1.313-.561.303-.375.458-.91.464-1.607v-.376c0-.722-.15-1.27-.449-1.64-.3-.375-.737-.562-1.313-.562h-.723zm9.316 3.018H26.34V23h-1.465v-7.11h4.629v1.187H26.34v1.836h2.812v1.182z" fill="#494C52"/></svg> -->
                                                <div class="d-flex flex-column align-items-start ml-16">
                                                    <p class="p2 text-grey-1 mb-1">{{ file[0].slice(0, -4) }}</p>
                                                    <p class="p5 text-grey-3 mb-0">{{ file[1] }}Mb</p>
                                                </div>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>
            </div>
        </div>
    </section>
</template>

<script>
import Preloader from '@/components/Preloader/Preloader'
import ProductsItem from './ProductsItem.vue'

export default {
    components: {
        ProductsItem, Preloader
    },
    data() {
        return {
            loading: true,
            products: [],
            selectedProduct: {},
            selectedIndex: null
        }
    },
    created() {
        this.getProducts()
    },
    methods: {
        selectProduct(product, index) {
            if (window.innerWidth > 991) {
                if (this.selectedProduct.id == product.id) {
                    this.selectedProduct = {};
                    this.selectedIndex = null;
                } else {
                    this.selectedProduct = product;
                    this.selectedIndex = index;
                }
            } else {
                this.selectedProduct = {};
                this.selectedIndex = null;
            }
        },
        getProducts() {
            this.axios
                .get('/static/products.json')
                .then(response => {
                    this.products = response.data.data;
                    this.loading = false;
                }).catch(error => {
                    this.$router.push({ name: 'PageNotFound' });
                });
        }
    },
}
</script>

<style lang="scss">
    .product {
        margin-bottom: 68px;
        &-info {
            display: flex;
            align-items: flex-start;
            padding: 24px 75px 24px 24px;
            background: $white;
            box-shadow: inset 8px 8px 15px rgba(198, 198, 198, 0.25);
            border-radius: 10px;
            max-height: 420px;
            overflow: auto;
            @media (max-width: 991px) {
                padding: 24px;
            }
            &::-webkit-scrollbar {
                width: 19px;
            }
            &::-webkit-scrollbar-thumb {
                border: 8px solid rgba(0, 0, 0, 0);
                background-clip: padding-box;
                border-radius: 100px;
                -webkit-border-radius: 100px;
                background-color: #BDBDBD;
            }
            &::-webkit-scrollbar-button {
                width: 0;
                height: 0;
                display: none;
            }
            &::-webkit-scrollbar-corner {
                background-color: transparent;
            }
            & .product-item {
                background: $bg;
                max-width: 295px;
                margin-bottom: 0;
                margin-right: 24px;
                pointer-events: none;
                @media (max-width: 991px) {
                    max-width: 100%;
                    margin-right: 0;
                }
            }
            &__links {
                display: flex;
                flex-direction: column;
            }
        }
    }
</style>